import { Directive, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';

import { ConfigLoaderService } from '../services/config-loader.service';

@Directive({
  selector: '[appThemeLoader]'
})
export class ThemeLoaderDirective {

  constructor(@Inject(DOCUMENT) private document: any, private configLoaderService: ConfigLoaderService) {
    const config = this.configLoaderService.config;

    if (!config['theme']) {
      config['theme'] = '/assets/css/styles.css';
    }
    const head = this.document.querySelector('head');
    const link = this.document.createElement('link');
    link.rel = 'stylesheet';
    link.href = config['theme'];
    head.appendChild(link);
    document.body.querySelector('#loading').classList.add('hidden');
    document.body.querySelector('app-root').classList.remove('hidden');
  }
}
