import { Component, AfterViewChecked, ViewChild, ElementRef, Input } from '@angular/core';
import { RoutesService } from '../../services/routes.service';
import { Config } from '../../services/config-loader.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements AfterViewChecked {

  @Input() config: Config;

  @ViewChild('headerEl') headerEl: ElementRef;

  @ViewChild('ticketButtonEl') ticketButtonEl: ElementRef;

  @ViewChild('dropdownEl') dropdownEl: ElementRef;
  readonly routes = this.routesService.get();

  constructor(
    private routesService: RoutesService
  ) {}

  openingDropdown(element) {
    element.classList.toggle('active');
    this.dropdownEl.nativeElement.classList.toggle('show');
  }

  ngAfterViewChecked() {
    this.headerEl.nativeElement.classList.remove('hidden');
    this.headerEl.nativeElement.classList.add('show');
    if (this.ticketButtonEl) {
      this.ticketButtonEl.nativeElement.classList.add('show');
    }
  }

}
