import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { ConfigLoaderService, Config } from './config-loader.service';

@Injectable({
    providedIn: 'root'
})
export class WpCategoriesApiService {

    prefixUrl: string;

    constructor(private http: HttpClient, private configLoaderService: ConfigLoaderService) {
        const wp = this.configLoaderService.config['wp'];
        this.prefixUrl = wp.protocol + wp.domain + wp.categories_api;
    }

    get(categoryId: string) {
        return this.http
        .get(this.prefixUrl + categoryId);
    }

}
