import { Component, Input, ViewChild, ElementRef, AfterViewChecked } from '@angular/core';
import { Config, ConfigLoaderService } from '../../services/config-loader.service';
import { WpCategoriesApiService } from '../../services/wp-categories-api.service';


@Component({
  selector: 'app-speakers',
  templateUrl: './speakers.component.html',
  styleUrls: ['./speakers.component.css']
})
export class SpeakersComponent implements AfterViewChecked {

  @Input() config: Config;

  @ViewChild('speakersEl') speakersEl: ElementRef;

  speakers;

  constructor(private configLoaderService: ConfigLoaderService, private wpCategoriesApiService: WpCategoriesApiService) {
    const config = this.configLoaderService.config;

    this.wpCategoriesApiService
      .get(config['categories'].speakers)
      .subscribe(posts => this.speakers = posts);
  }

  ngAfterViewChecked() {
    this.speakersEl.nativeElement.classList.add('show');
  }

}
