import { Component, Input, AfterViewChecked, ViewChild, ElementRef } from '@angular/core';
import { Config, ConfigLoaderService } from '../../services/config-loader.service';
import { WpCategoriesApiService } from '../../services/wp-categories-api.service';

@Component({
  selector: 'app-location',
  templateUrl: './location.component.html',
  styleUrls: ['./location.component.css']
})
export class LocationComponent implements AfterViewChecked {

  @Input() config: Config;

  @ViewChild('locationEl') locationEl: ElementRef;

  mission;

  constructor(private configLoaderService: ConfigLoaderService, private wpCategoriesApiService: WpCategoriesApiService) {
    const config = this.configLoaderService.config;

    this.wpCategoriesApiService
      .get(config['categories'].location)
      .subscribe(posts => this.mission = posts[0].content.rendered);
  }

  ngAfterViewChecked() {
    this.locationEl.nativeElement.classList.add('show');
  }
}
