import {
  Component,
  Input,
  AfterViewChecked,
  ViewChild,
  ElementRef,
  Sanitizer,
  SecurityContext,
} from '@angular/core';
import { Config, ConfigLoaderService } from '../../services/config-loader.service';
import { WpCategoriesApiService } from '../../services/wp-categories-api.service';

@Component({
  selector: 'app-mission',
  templateUrl: './mission.component.html',
  styleUrls: ['./mission.component.css']
})
export class MissionComponent implements AfterViewChecked {

  @Input() config: Config;

  @ViewChild('missionEl') missionEl: ElementRef;
  @ViewChild('missionContentEl') missionContentEl: ElementRef;

  mission;

  constructor(
    private configLoaderService: ConfigLoaderService,
    private wpCategoriesApiService: WpCategoriesApiService,
    private sanitizer: Sanitizer) {
    const config = this.configLoaderService.config;

    this.wpCategoriesApiService
      .get(config['categories'].mission)
      .subscribe(posts => this.mission = posts[0].content.rendered);
  }

  ngAfterViewChecked() {
    this.missionEl.nativeElement.classList.add('show');
    this.missionContentEl.nativeElement.innerHTML = this.sanitizer.sanitize(SecurityContext.HTML, this.mission);
  }
}
