import { Component, Input, AfterViewChecked, ViewChild, ElementRef } from '@angular/core';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';

import { Config } from '../../services/config-loader.service';

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.css']
})
export class LandingComponent implements AfterViewChecked {

  @Input() config: Config;

  @ViewChild('landingEl') landingEl: ElementRef;

  constructor() {}

  ngAfterViewChecked() {
    this.landingEl.nativeElement.classList.add('show');
  }

}
